.quiz-page-wrapper{
  max-width: 90%;
  margin: auto;
  position: relative;
}

#quiz-title{
  margin-top: 0%;
  margin-bottom: 2%;
}

#question-number{
  position: absolute;
  top:0%;
  right: 0%;
}

#question-prompt{
  margin-top: 2%;
  margin-bottom: 2%;
  margin-left: auto;
  margin-right: auto;
  max-width: 75%;
  text-align: center;
}


#answers-wrapper{
  max-width:55%;
  margin-left: auto;
  margin-right: auto;
}
.answer-item{
  cursor: pointer;
}

#submit-answer-btn{
  margin-top: 3%;
  margin-left: auto;
  margin-right: auto;
  width: 30%;
  text-align: center
}
